<template>
<main>
    <Header :header="content"/>
    <input type="text" placeholder="نام دستگاه یا بارکد مدنظر را جستجو نمایید ..." v-model="filter" style="width:500px;display:block;margin:10px auto;font-family:yekan;"/>
    <div class="container list">
        <table class="table table-hover container" >
            <thead>
                <tr>
                <th scope="col">ردیف</th>
                <th scope="col">نام دستگاه</th>
                <th scope="col">برند</th>
                <th scope="col">بارکد</th>
                <th scope="col">نوع</th>
                <th scope="col">وضعیت</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(device,index) in filteredRows" :key="device">
                <th scope="row">{{index+1}}</th>
                <td>{{device.name}}</td>
                <td>{{device.brand}}</td>
                <td>{{device.barcode}}</td>
                <td>{{device.type}}</td>
                <td>{{device.status}}</td>
                </tr>
            </tbody>
        </table>
    </div>
 
</main>
</template>

<script>
import Header from '../../components/Header.vue'

export default {
    name:'DeviceStatus',
    components:{Header},
    data() {
        return {
            content:'وضعیت دستگاه ها',
            filter:'',
            devices:[
                {
                name:'washer',
                brand:'xxxxxx',
                barcode:123456,
                type:'شستشو',
                status:'در حال استفاده',
                },
                {
                name:'sterille',
                brand:'qqqqqq',
                barcode:563241,
                type:'استریل',
                status:'در حال تعمیر',
                },
                {
                name:'pack',
                brand:'uuuuuuu',
                barcode:569043,
                type:'بسته بندی',
                status:'خراب',
                },  
            ] 
        }
    },
    computed: {
         filteredRows() {
            return this.devices.filter(device => {
            const name = device.name.toString().toLowerCase();
            const barcode = device.barcode.toString();
            const searchTerm = this.filter.toLowerCase();

            return name.includes(searchTerm) ||
            barcode.includes(searchTerm);
            });
         }
    },
    
      
}
</script>

<style lang="scss" scoped>
.list{
        max-height: 65vh;
        overflow-y: scroll;
        table{
            
            direction: rtl;
            background-color: rgb(255, 255, 255);
            border-radius: 2px;
            text-align: center;
    
                thead{
                    background-color: #082C68;
                    color: rgb(255, 255, 255);
                    
        
                }
                    
        }
    }
</style>